.random-mm-wrap {
  /* text-align: center; */
  height: 100%;
  width: 100%;
}
.random-mm-wrap .refresh {
  position: fixed;
  top: 50px;
  left: 50%;
  z-index: 999;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  border: 1px solid gray;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 6px;
  line-height: 50px;
  text-align: center;
  background: rgba(234, 245, 200, 0.5);
}
.random-mm-wrap .main {
  height: 100%;
  width: 100%;
}



