/* Base Application Styles */
html,
body {
  height: 100%;
  width: 100%;
  background-color: #ddd;
  /* background-color: #222; */
}
.app-wrap {
  height: 100%;
  width: 100%;
}
.app-wrap .nav-header {
  background: #eee;
  padding: 0 30px;
}
.app-wrap .nav-header li {
  list-style: none;
  display: inline-block;
  padding: 4px;
}
.app-wrap .nav-header li.active > a {
  color: wheat;
  background: black;
}
.app-wrap .app-main {
  height: 100%;
  width: 100%;
}



