.list-wrap {
  /* text-align: center; */
  height: 100%;
  width: 80%;
  margin: 0 auto;
}
.list-wrap .filter {
  background: #eee;
  padding: 4px 4px;
}
.list-wrap .filter .filter-item {
  float: left;
  padding: 4px;
  margin-left: 8px;
  min-width: 64px;
  text-align: center;
}
.list-wrap .filter .filter-item + .filter-item {
  border-left: 1px solid #aaa;
}
.list-wrap .filter .filter-item.active {
  color: aliceblue;
  background: green;
}
.list-wrap .filter:after {
  content: '';
  display: block;
  clear: both;
}
.list-wrap .list {
  padding: 4px 4px;
  background: #fff;
}
.list-wrap .list .list-item {
  background: #fff;
  border-top: 1px solid #f0f0f0;
  position: relative;
  padding: 10px 0 10px 10px;
  font-size: 14px;
  padding-right: 10px;
}
.list-wrap .list .list-item:hover {
  background: #f5f5f5;
}
.list-wrap .pagination {
  background: #fff;
  padding: 4px 4px;
}



