@font-face {
  font-family: "icons-turn-arrow";
  src: url('../../fonts/icons/iconfont.eot');
  /* IE9*/
  src: url('../..//fonts/icons/iconfont.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */ url('../../fonts/icons/iconfont.woff') format('woff'), /* chrome、firefox */ url('../../fonts/icons/iconfont.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/ url('../../fonts/icons/iconfont.svg#iconfont') format('svg');
  /* iOS 4.1- */
}
/* stage -- start */
.stage {
  position: relative;
  width: 100%;
  height: 100%;
}
/* stage -- start */
/* image -- start */
.img-sec {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-perspective: 1800px;
          perspective: 1800px;
  background-color: #ddd;
}
.img-sec .img-figure {
  position: absolute;
  width: 320px;
  height: 520px;
  margin: 0;
  padding: 40px 10px;
  background-color: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: left 0.6s ease-in-out, top 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  transition: left 0.6s ease-in-out, top 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  -o-transition: transform 0.6s ease-in-out, left 0.6s ease-in-out, top 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, left 0.6s ease-in-out, top 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, left 0.6s ease-in-out, top 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  -webkit-box-shadow: 2px 2px 5px;
          box-shadow: 2px 2px 5px;
  -webkit-transform-origin: 0 50% 0;
      -ms-transform-origin: 0 50% 0;
          transform-origin: 0 50% 0;
}
.img-sec .img-figure.is-inverse {
  -webkit-transform: rotateY(180deg) translateX(-100%);
          transform: rotateY(180deg) translateX(-100%);
}
.img-sec .img-figure img {
  width: 100%;
  height: auto;
}
.img-sec figcaption {
  text-align: center;
}
.img-sec figcaption .img-title {
  margin: 20px 0 0 0;
  color: #a7a0a2;
  font-size: 16px;
}
.img-sec figcaption .img-back {
  background-color: rgba(255, 255, 255, 0.9);
  -webkit-transform: rotateY(180deg) translateZ(1px);
          transform: rotateY(180deg) translateZ(1px);
  position: absolute;
  left: 0;
  top: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 50px 40px;
  overflow: auto;
  font-size: 25px;
  line-height: 1.25;
  text-align: left;
  word-wrap: break-word;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.img-sec figcaption .img-back p {
  margin: 0;
}
/* image -- end */
/* controller --start */
.controller-nav {
  position: absolute;
  left: 0;
  bottom: 30px;
  z-index: 101;
  width: 100%;
  text-align: center;
}
.controller-nav .controller-unit {
  display: inline-block;
  margin: 0 5px;
  width: 30px;
  height: 30px;
  text-align: center;
  cursor: pointer;
  background-color: #aaa;
  border-radius: 50%;
  vertical-align: middle;
  -webkit-transform: scale(0.5);
      -ms-transform: scale(0.5);
          transform: scale(0.5);
  -webkit-transition: background-color 0.3s, -webkit-transform 0.6s ease-in-out;
  transition: background-color 0.3s, -webkit-transform 0.6s ease-in-out;
  -o-transition: transform 0.6s ease-in-out, background-color 0.3s;
  transition: transform 0.6s ease-in-out, background-color 0.3s;
  transition: transform 0.6s ease-in-out, background-color 0.3s, -webkit-transform 0.6s ease-in-out;
}
.controller-nav .controller-unit.is-center {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  background-color: #888;
}
.controller-nav .controller-unit.is-center::after {
  color: #fff;
  content: "\e600";
  font-family: "icons-turn-arrow" !important;
  font-style: normal;
  line-height: 30px;
  font-size: 80%;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: grayscale;
}
.controller-nav .controller-unit.is-center.is-inverse {
  background-color: #555;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
/* controller --end */



